import React, { useState } from "react";

function Carousel(props){
    const {firstImage,secondImage,thirdImage,display} = props;
    const [open, setOpen] = useState(false);
    function handleClick(event){
        console.log(event.target.ariaHidden)
        if(event.target.ariaHidden==="bg"){
            props.onBGClick();
        }
    }
    
    return(
        

        <div onClick={handleClick}>

    <div style={{display:display}} id="carouselExampleFade" aria-hidden="bg" className="carousel slide carousel-fade" data-ride="carousel">
        <div className="carousel-inner">
            <div aria-hidden="bg" className="carousel-item active">
                <img className="carousel-image" src={firstImage} alt="First slide"/>
            </div>
            <div aria-hidden="bg" className="carousel-item">
                <img className="carousel-image" src={secondImage} alt="Second slide"/>
            </div>
            <div aria-hidden="bg" className="carousel-item">
                <img className="carousel-image" src={thirdImage} alt="Third slide"/>
            </div>
        </div>
        <a className="carousel-control-prev" href="#carouselExampleFade" role="button" data-slide="prev">
            <span className="carousel-control-prev-icon" aria-hidden="true"></span>
            <span className="sr-only">Previous</span>
        </a>
        <a className="carousel-control-next" href="#carouselExampleFade" role="button" data-slide="next">
            <span className="carousel-control-next-icon" aria-hidden="true"></span>
            <span className="sr-only">Next</span>
        </a>
    </div>
    <div className="panel"></div>
        </div>
        
    );
}
export default Carousel;
