import React, { useState } from "react";
import emailjs from 'emailjs-com';

function Form(){
    const [formValues,setFormValues]=useState({
        fullName:"",
        email:"",
        category:"",
        subject:"",
        description:""
    })

    function handleChange(event){
        const {value, name} = event.target;

        setFormValues(prevValues =>{
            if(name==="fullName"){
                return{
                    ...prevValues,
                    fullName:value,
                }
            }
            else if(name==="email"){
                return{
                    ...prevValues,
                    email:value
                }
            }
            else if(name==="category"){
                return{
                    ...prevValues,
                    category:value
                }
            }
            else if(name==="description"){
                return{
                    ...prevValues,
                    description:value
                }
            }
            else if(name==="subject"){
                return{
                    ...prevValues,
                    subject:value
                }
            }
        });
        console.log(formValues);
    }

    const handleSubmit = (e) => {
        console.log(process.env.REACT_APP_SERVICE_ID)
        e.preventDefault(); // Prevents default refresh by the browser
        emailjs.send(process.env.REACT_APP_SERVICE_ID,process.env.REACT_APP_TEMPLATE_ID, formValues,process.env.REACT_APP_USER_ID )
        .then((result) => {
        alert("Message Sent, We will get back to you shortly", result.text);
        },
        (error) => {
        alert("An error occurred, Please try again", error.text);
        });
        };

    return(
    <form id="form" onSubmit={handleSubmit}>
            <div className="form-group">
            <label for="email">Email</label>
            <input onChange={handleChange} type="email" className="form-control" name="email" id="email" placeholder="Email" />
            </div>
            <div className="form-group">
            <label for="fullName">Full Name</label>
            <input onChange={handleChange} type="text" className="form-control" name="fullName" id="fullName" placeholder="Full Name" />
            </div>
        <div className="form-group">
            <label for="category">Category</label>
            <select onChange={handleChange} name="category" id="category" className="form-control">
                <option selected>Choose...</option>
                <option>Website</option>
                <option>Web Design</option>
                <option>Game</option>
                <option>Hiring</option>
            </select>
        </div>

        <div className="form-group">
            <label for="subject">Subject</label>
            <input onChange={handleChange} value={"["+formValues.fullName+"] "+formValues.category} name="subject" type="text" className="form-control" id="subject" placeholder="Subject"/>
        </div>

        


        <div className="form-group">
            <label for="description">Description</label>
            <textarea onChange={handleChange} className="form-control" id="description" name="description" rows="4"></textarea>
        </div>
        <div className="form-group">

        <button type="submit" name="submit" className="btn btn-dark pl-5 pr-5">SEND EMAIL</button>
        </div>
    </form>
    );
}
export default Form;