import React from "react";
import ContactMailIcon from '@material-ui/icons/ContactMail';
import LocationOnIcon from '@material-ui/icons/LocationOn';
import Form from "./Form";
import Zoom from "@material-ui/core/Zoom";
import Paper from '@material-ui/core/Paper';
function Contact(){
    return(
        <section id="contact">

            <div className="container-fluid">
            <div className="row">
                <h1 className="col-lg-12 contact-title">Let's have a conversation</h1>
            </div>
            </div>
            <Zoom in={true} style={{transitionDelay: '500ms'}}>
                <Paper>
            <div className="contact-form row">
            
                    <div className="col col-lg-5 col-md-6 contact-left">
                        <h1>GET IN TOUCH</h1>
                        <h2><ContactMailIcon style={{ fontSize: 40 ,opacity:'0.8'}} /></h2>
                        <span>fadifarah112233@gmail.com</span>
                        <h2><LocationOnIcon style={{ fontSize: 40 ,opacity:'0.8'}} /></h2>
                        <span>Khalet e-Deir, Nazareth</span>
                    </div>
                <div className="col col-lg-4 col-md-6 contact-right">
                    <Form />
                </div>
                
            </div>
                </Paper>
            </Zoom>
        </section>
    );
}
export default Contact;