import React from "react";
import Zoom from "@material-ui/core/Zoom";
import Paper from '@material-ui/core/Paper';

function About(){
    function GetMyAge(){
        var dob = new Date("12/29/1998");   
        var month_diff = Date.now() - dob.getTime();   
        var age_dt = new Date(month_diff);   
        var year = age_dt.getUTCFullYear();   
        var age = Math.abs(year - 1970);  
    return age;
    }
 
    
    return (
        <section id="about">
        
        <div style={{backgroundColor:'white'}}  className="container-fluid">
        
            <div  className="row">
                <div className="about-image col col-lg-6 col-md-12">
                </div>
        
                <img className="about-img" src="Screenshot_20200429-190934_VideoPlayer.jpg"></img>
                <div className="about-details col col-lg-6 col-md-12">
                <Zoom in={true} style={{transitionDelay: '500ms'}}>
                <Paper elevation={0} style={{background:'none' }}>
                    <h1 className="about-title hover-underline-animation">ABOUT</h1>
                    <h1>__</h1>
                    <div className="about-description">
                        <div>My name is Fadi. I'm a Web and Game developer located in Nazareth, IL.</div>
                        <div>I'm a well-organised and highly motivated type of person, problem solver, with the ability of high attention to detail.</div>
                        <div>Interested in the entire frontend and backend development spectrum plus working on ambitious projects with positive people.</div>
                        <div className="about-personal-details row">
                            <hr/>
                            <div className="col-lg-6 col-md-12">
                                <div><h4>Birthday: </h4> <span>29 Dec 1998</span></div>
                                <div><h4>City: </h4> <span>Nazareth, IL</span></div>
                                <div><h4>Degree: </h4> <span>(Diploma) Software Engineering</span></div>
                            </div>
                            <div className="col-lg-6 col-md-12">
                                <div><h4>Age: </h4> <span>{GetMyAge()}</span></div>
                                <div><h4>Email: </h4> <span>fadifarah112233@gmail.com</span></div>
                                <div><h4>Freelance: </h4> <span>Available</span></div>
                            </div>
                            
                        </div>
                    </div>

                    <div className="skill row">
                        <div className="col-lg-12">
                                <h1>SKILL</h1>
                                <div className="html">
                                    <div className="progress-header">HTML 90%</div>
                                    <hr className="progress"></hr> 
                                    <hr style={{width:'45%'}} className="sub-progress"></hr> 
                                </div>
                                <div className="css">
                                    <div className="progress-header">CSS3 85%</div>
                                    <hr className="progress"></hr> 
                                    <hr style={{width:'42.5%'}} className="sub-progress"></hr> 
                                </div>
                                <div className="javascript">
                                    <div className="progress-header">JAVASCRIPT 90%</div>
                                    <hr className="progress"></hr> 
                                    <hr style={{width:'45%'}} className="sub-progress"></hr> 
                                </div> 
                                <div className="node">
                                    <div className="progress-header">NODEJS 75%</div>
                                    <hr className="progress"></hr> 
                                    <hr style={{width:'37%'}} className="sub-progress"></hr> 
                                </div>           
                        </div>
                    </div>
                </Paper>
            </Zoom>
                </div>
            </div>
        
        </div>
        </section>
    );
}
export default About;