import React from "react";
function Navbar(props){
    function handleClick(event){
        props.onClick(event.target.innerHTML)
    }
    return(
        <nav className="navbar navbar-light" >
        <a className="navbar-brand text-light">Fadi</a>
        <div className="pc-navbar navbar-items-block">
        <ul>
            <li><a className="navbar-clicks" onClick={handleClick} href="#home">Home</a></li>
            <li><a className="navbar-clicks" onClick={handleClick} href="#about">About</a></li>
            <li><a className="navbar-clicks" onClick={handleClick} href="#contact">Contact</a></li>
            <li><a className="navbar-clicks" onClick={handleClick} href="#portfolio">Portfolio</a></li>

        </ul>
        </div>

        <div className="mobile-navbar">
        <div className="collapse navbar-items-block" id="navbarToggleExternalContent">
        <ul>
            <li><a className="navbar-clicks" onClick={handleClick} href="#home">Home</a></li>
            <li><a className="navbar-clicks" onClick={handleClick} href="#about">About</a></li>
            <li><a className="navbar-clicks" onClick={handleClick} href="#contact">Contact</a></li>
            <li><a className="navbar-clicks" onClick={handleClick} href="#portfolio">Portfolio</a></li>

        </ul>
        </div>
        <button className="navbar-toggler navbar-list-btn" type="button" data-toggle="collapse" data-target="#navbarToggleExternalContent" >
            <span className="navbar-toggler-icon"></span>
        </button>
        </div>
      </nav>
    );
}
export default Navbar;