import React from "react";
import About from "../About/About"
import Fab from "@material-ui/core/Fab";
import Zoom from "@material-ui/core/Zoom";
import Paper from '@material-ui/core/Paper';





function Header(props){
  function handleClick(event){
    props.props.onClick("About")
  }
    return(
  <div>


    <Zoom in={true} style={{transitionDelay: '500ms'}}>
      <Paper elevation={0} style={{background:'none' }}>
        <h1 className="big-heading">Fadi Farah</h1>
        <h1 className="small-heading">I'm Web and Game Developer</h1>
      </Paper>
    </Zoom>
    <Zoom in={true} style={{transitionDelay:'1000ms'  }}>
        <Fab href="#about" onClick={handleClick} style={{padding:'0 5%', marginTop:'30px',fontFamily: 'Krona One',outline:'none' }} variant="extended">About Me</Fab>
    </Zoom>
  </div>
    );
}
export default Header