import React, { useState } from "react";
import Card from "./Card";
import Carousel from "./Carousel";

var images={
    firstImage:"",
    secondImage:"",
    thirdImage:""
}
function Portfolio(){
    const [displayStatus,setDisplayStatus]=useState("none");
    
   function handleClick(event){
       
        if(event.target.name==="first"){
            images={
                firstImage:"../images/1.png",
                secondImage:"../images/2.png",
                thirdImage:"../images/3.png"
            }
        }
        else if(event.target.name==="second"){
            images={
                firstImage:"../images/GameMap1_1080p_24.05.2021_01-07-42.jpg",
                secondImage:"../images/GameMap1_1080p_24.05.2021_01-06-20.jpg",
                thirdImage:"../images/GameMap1_1080p_24.05.2021_01-10-15.jpg"
            }
        }
        else if(event.target.name==="third"){
            images={
                firstImage:"../images/CAPTURE1.png",
                secondImage:"../images/CAPTURE2.png",
                thirdImage:"../images/CAPTURE3.png"
            }
        }
        setDisplayStatus("initial");
    }
    

    function handleBGClick(){
        setDisplayStatus("none");
    }
    return(
        <section id="portfolio">

            <div className="row">
                <h1 className="portfolio-header col-lg-12">Portfolio</h1>
                <p className="col-lg-12 portfolio-sub-header">⊷Some of the things that I've done⊶</p>
                <hr></hr>
            </div>
            <div className="container">
            <div className="portfolio-row row">
                <Card
                    name="first"  
                    header="Hotel Web Design"
                    src="../images/1.png"
                    subject="Web Design "
                    date="/ 05 Mar, 2020"
                    onClick={handleClick}
                />
                <Card
                    name="second"                 
                    header="3D Multiplayer Game"
                    src="../images/GameMap1_1080p_24.05.2021_01-07-42.jpg"
                    subject="Unity Game Development "
                    date="/ 17 Jun, 2019"
                    onClick={handleClick}
                />
                <Card  
                    name="third"
                    header="A simple Web App"
                    src="../images/CAPTURE1.png"
                    subject="Web Development "
                    date="/ 17 Feb, 2021"
                    onClick={handleClick}
                />  

                <Carousel onBGClick={handleBGClick} display={displayStatus} firstImage={images.firstImage} secondImage={images.secondImage} thirdImage={images.thirdImage} />
                
            </div>
            </div>
          
        </section>
    );
}
export default Portfolio;