import React from 'react';

import Header from "./Header";

function Home(props){
    return(
        <section className="colored-section" id="title">
    <div className="intro container-fluid">
      <div className="row mt-5">
        <div className="intro-details col-lg-12">
          
        <Header props={props}/>
          
        </div>
      </div>
    </div>
  </section>
    );
}
export default Home;