import React from "react";

function Card(props){
    const {name,src,header,subject,date}=props;
    return (
        <div className="portfolio-card col-lg-4 col-md-12">
            <img name={name} onClick={props.onClick} className="card-image" src={src}></img>
            <div className="card-details">
                <h1 className="card-header">{header}</h1>
                <div><span className="card-subject">{subject}</span><span className="card-date">{date}</span></div>
            </div>
        </div>
    );
}
export default Card;